/* Universal Reset of elements and border-box to keep things neat*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --light-green: #01c380;
  --dark-green: #010D00;
  --shadow: #0C2605;
  --green: #2E590E;
  --text: #e2e2e2;
  --button-text: #a9cdf7;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

@keyframes buttonFade {
  from {background: white;}
  to {background: var(--light-green);}
}

a {
  position: relative;
  display: inline-block;
  padding: 25px 30px;
  margin: 0;
  color: #03e9f4;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 4px;
  overflow: hidden;
  margin-right: 50px;
}

a:hover {
  animation-name: buttonFade;
  animation-duration: 500ms;
  background: var(--light-green);
}

a span {
  position: absolute;
  display: block;
}

body {
  font-family: Helvetica, Arial, sans-serif;
}

/* Adjust spacing from the top of the page and move anchors to the right */
header {
  padding: 20px 35px;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 8px 8px 0 black;
  background: rgb(25,30,41);
  background: linear-gradient(145deg, rgba(25,30,41,1) 0%, rgba(0,0,0,1) 60%, rgba(0,153,100,1) 100%);
}

header, header * {
  text-shadow: 2px 2px var(--shadow);
}

header ul li {
  display: inline-block;
  padding: 10px;
  font-weight: bold;
  list-style: none;
}

header ul li a:hover {
  color: white;
}

body {
  background: rgb(25,30,41);
}

main {
  text-align: center;
  padding-top: 2rem;
}

h2 {
  color: var(--light-green);
  text-align: center;
  padding-top: 1.5rem;
  text-shadow: 2px 2px var(--shadow);
}

p {
  color: white;
  font-weight: bold;
  text-shadow: 2px 2px var(--shadow);
}

footer {
  padding: 20px 35px;
  align-items: center;
  position: fixed;
  display: flex;
  width: 100%;
  bottom: 0px;
  justify-content: space-between;
  box-shadow: 0 8px 8px 0 black;
  background: rgb(25,30,41);
  background: linear-gradient(145deg, rgba(25,30,41,1) 0%, rgba(0,0,0,1) 60%, rgba(0,153,100,1) 100%);
}

footer, footer * {
  text-shadow: 2px 2px var(--shadow);
}

footer ul li {
  display: inline-block;
  padding: 10px;
  font-weight: bold;
  list-style: none;
}

footer ul li a:hover {
  color: white;
}

.me h1 {
  color: #03e9f4;
}

.dropdown-content {
  list-style-type: none;
  display: none;
  position: absolute;
  z-index: 1;
  background-color: var(--dark-green);
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.dropdown-content a {
  width: 100%;
}

.about {
  display: flex;
  justify-content: center;
  width: 100%;
}

.hero-image img {
  padding-top: 15px;
  padding-bottom: 25px;
  width: 70rem;
  height: 30rem;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.projects h2 {
  color: #03e9f4;
}

.projects .card-title {
  color: var(--button-text);
}

.btn {
  display: flex;
  justify-content: center;
  width: 40%;
  color: var(--button-text);
  margin-left: auto;
  margin-right: auto;
  font-weight: bold;
  border: 2px solid #009964;
  text-shadow: 1px 1px var(--shadow);
  box-shadow: 1px 1px black;
}

.about btn {
  margin-right: 0;
}

.about h2 {
  color: #03e9f4;
}

.about .card-img {
  padding-top: 2.5rem;
}

.card {
  background: transparent;
  border: none;
}

.project-text {
  text-align: justify;
  padding-top: 3%;
  padding-left: 20%;
  padding-right: 20%;
  padding-bottom: 3%;
}

.about-me {
  text-align: justify;
}

.card-group h3 {
  text-align: center;
  color: turquoise;
  text-shadow: 2px 2px var(--shadow);
}

.card-group img {
  height: 10rem;
  width: 20rem;
  margin-left: auto;
  margin-right: auto;
}

.contact {
  font-weight: bold;
  text-shadow: 1px 1px var(--shadow);
  list-style-type: none;
  color: var(--light-green);
  padding: 20px;
  margin: 3.5%;
  width: 100%;
  text-align: center;
}

.contact  h3 {
  color: #03e9f4;
}

.contact-icons {
  max-width: 10%;
  max-height: 10%;
  padding-right: 25px;
}

.skills {
  text-align: center;
  color: var(--light-green);
  text-shadow: 1px 1px var(--shadow);
  margin: 3.5%;
}

.skills li {
  list-style-type: none;
  color: var(--button-text);
}

.skills h3 {
  margin-top: 2%;
  font-size: 32px;
  color: #03e9f4;
}

.skills a {
  margin: 3.5%;
  width: 40%;
  color: var(--button-text);
}

.skills img {
  max-width: 10%;
  max-height: 5%;
  padding-right: 25px;
  padding-bottom: 25px;
}

.contact-icons {
  max-width: 10%;
  max-height: 10%;
  padding-right: 25px;
}

.credits-list {
  padding-top: 5%;
  list-style-type: none;
  text-align: center;
}


@media screen and (max-width: 768px) {
  .hero-image img {
      width: 35rem;
      height: 15rem;
  }
  .about img {
      padding-top: 5rem !important;
      width: 10rem;
  }
  .about p {
      width: 20rem;
  }
}

@media screen and (max-width: 575px) {
  * {
    margin: 0;
    padding: 0;
  }

  header {
    display: revert;
  }

  header ul {
    display: none;
  }

  .me h1 {
    text-align: center;
  }

  .me a {
    margin: 0;
  }

  .me:hover .dropdown-content {
    display: block;
  }

  .hero-image img {
    width: 20rem;
    height: 15rem;
  }
  .about img {
    width: 10rem;
  }
  .about p {
    width: 10rem;
  }

  .projects img {
    width: 80%;
  }

  .projects a {
    width: 80%;
  }

  .projects p {
    width: 100%;
    word-spacing: 0%;
  }
  
  .contact li a {
    width: 100%;
    text-align: left;
  }

  .contact li img {
    max-width: 25%;
  }

  .skills li img {
    max-width: 25%;
  }

  .resume-text a {
    display: revert;
  }
}